.headerContainer {
    background-color: rgba(0, 100, 235, 1);
    height: 10vh;
    min-width: 320px;
    display: flex;
    align-items: center;
}

.headerContainer img {
    height: 55%;
    margin-left: 3%;
}

.headerContainer input {
    height: 55%;
    width: 31%;
    min-width: 300px;
    margin-left: 2%;
    border: none;
    border-radius: 6px;
    outline: none;
    font-size: 14px;
    font-family: 'Inter';
    font-weight: 400;
    background: url(../../assets/images/search.png) no-repeat 4.6% 54%;
    background-size: auto 40%;
    background-color: white;
    padding-left: 4%;
}

@media (max-width: 700px) {
    .headerContainer input {
        background: none;
        background-color: white;
        margin-left: 1%;
    }
}

@media (max-width: 500px) {
    .headerContainer img {
        display: none;
    }
}
